h3 {
  margin-bottom: 0.0rem !important;
}

.title {
  font-size: 1.7rem;
  color: turquoise;
  padding-left: 1rem;
  padding-bottom: none;
}

.subtitle {
  font-size: 1rem;
  color: turquoise;
}

.box {
  border: 3px solid currentColor;
  border-radius: 10px;
  margin: .05rem;
} 

.col {
  max-width: 49% !important;
}

.grid {
  width: 100%;
  height: 0vh;
  display: flex;
  flex-wrap: wrap;
 
}


.App-side {
  background-color: #282c34;
  z-index: 11 !important;

  opacity: 0.95;
  height: 100vh;  
  width: 100vw; 

  font-size: calc(20px + 2.5vmin);
  color: rgb(45, 182, 130);
  border: 1px solid turquoise;
  border-radius: 5px;
}


@media only screen and (min-width: 900px) {
  .App-side {
    width: 25vw;
   
    /* height: auto; */
  }
}

.button {
  height: 3.5rem;
  width: 6rem;
  z-index: 1;
}

.button:hover {
  background-color: rgb(31, 174, 160) !important;
}

.visible {
  z-index: 10;
}


.hidden {
  display: none;
}




.asia:focus,
.southamerica:focus,
.europe:focus,
.northamerica:focus,
.oceania:focus,
.africa:focus,
.global:focus {
  background: rgb(45, 182, 130) !important;
  color: black !important;
}

.asia {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 28%;
  margin-left: 30px;
}

.southamerica {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 35%;
  margin-left: 30px;
}

.europe {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 42%;
  margin-left: 30px;
}

.africa {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 49%;
  margin-left: 30px;
}

.oceania {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 56%;
  margin-left: 30px;
}

.northamerica {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 63%;
  margin-left: 30px;

}

.global {
  display: block;
  position: absolute;
  background: #212529 !important;
  bottom: 70%;
  margin-left: 30px;
}








